	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Domain Grabbing","date":"27 Jan 2000","slug":"/2000-01-27-domain-grabbing/","tags":"Y2000"},"html":"<p><a href=\"http://www.emode.com/tests/dog2.html\">What Breed of Dog Are You?</a> I am a German Shephard. \"You are a no-frills type of person. You are a hard-working and focused individual who doesn't need much pampering.\" Woof woof.</p>\n<p><a href=\"http://albums.photopoint.com/j/AlbumList?u=185392\">PhotoPoint</a>: Pictures from Weeks 4 and 5</p>"},"next":{"fields":{"title":"Yet Another One","date":"26 Jan 2000","slug":"/2000-01-26-yet-another-one/"}},"previous":{"fields":{"title":"Friday Afternoon","date":"28 Jan 2000","slug":"/2000-01-28-friday-afternoon/"}}},{"node":{"fields":{"title":"Yet Another One","date":"26 Jan 2000","slug":"/2000-01-26-yet-another-one/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/webcamhh.jpg\" height=\"288\" width=\"352\" border=\"0\" alt=\"webcamhh.jpg: \" /></p>\n<p>Here's a shot from our webcam at work, with the Hamburg harbor in the background. You can see my office, or at least the light in my office (circle). <i>But you can't see me!</i></p>\n<p>Is another EditThisPage baby on the way? http://orpheus.editthispage.com/ (in German)</p>\n<p>Yet another German family website on EditThisPage: http://familieberg.editthispage.com/</p>"},"next":{"fields":{"title":"A World Full of Colors","date":"25 Jan 2000","slug":"/2000-01-25-a-world-full-of-colors/"}},"previous":{"fields":{"title":"Domain Grabbing","date":"27 Jan 2000","slug":"/2000-01-27-domain-grabbing/"}}},{"node":{"fields":{"title":"A World Full of Colors","date":"25 Jan 2000","slug":"/2000-01-25-a-world-full-of-colors/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/crh0125.jpg\" height=\"361\" width=\"350\" border=\"0\" alt=\"crh0125.jpg: \" /></p>\n<p>We're starting to notice advances in Christopher's development. He now seems able to focus on the colored blocks we hung over his cradle. He's gaining control of his arms and hands, although they aren't quite coordinated yet. And he's started smiling (really smiling, not just the instinctive grin when he's just finished eating).</p>\n<p>Grandma Ruth is coming again tomorrow for a couple days. Grandma Shirley and Grandpa Marlyn have their plane tickets for the end of March. It looks like we'll have the baptism on 2 April in Jesteburg.</p>\n<p>We've gotten a lot of compliments on Christopher's sweater. It was knitted by Aunt Kirsten for her son Jakob, and we have it on loan.</p>"},"next":{"fields":{"title":"Monday Monday","date":"24 Jan 2000","slug":"/2000-01-24-monday-monday/"}},"previous":{"fields":{"title":"Yet Another One","date":"26 Jan 2000","slug":"/2000-01-26-yet-another-one/"}}}],"pathPrefix":"","first":false,"last":false,"index":1086,"pageCount":1101}}

